/// <reference types="@angular/localize" />

import { Component, EventEmitter, inject, Input, Output } from '@angular/core';
import { NgStyle } from '@angular/common';
import { MatButtonModule } from '@angular/material/button';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatIconModule } from '@angular/material/icon';
import { MatDividerModule } from '@angular/material/divider';
import { Router } from '@angular/router';
// eslint-disable-next-line @nx/enforce-module-boundaries
import { ShareStateService } from '@slowautomobile/exercises';

@Component({
  selector: 'ui-page-toolbar',
  standalone: true,
  imports: [
    NgStyle,
    MatButtonModule,
    MatTooltipModule,
    MatDividerModule,
    MatIconModule,
  ],
  templateUrl: './page-toolbar.component.html',
  styleUrl: './page-toolbar.component.scss',
})
export class PageToolbarComponent {
  router = inject(Router);
  private shareStateService = inject(ShareStateService);

  newButtonString = $localize`:@@PageToolbar.newBtn:New`;
  isLoading = this.shareStateService.isLoading;

  @Input() pageTitle?: string;
  @Input() pageType?: string;
  @Input() cancelButton = false;
  @Input() submitButton = false;
  @Input() newButton = false;
  @Input() editMode = false;
  @Input() itemName = '';
  @Input() clientName = '';
  @Input() isClientProgram = false;
  @Input() reuseProgram = false;

  @Output() submitEvent = new EventEmitter<void>();
  @Output() newEvent = new EventEmitter<void>();
  @Output() cancelEvent = new EventEmitter<void>();
  @Output() deleteEvent = new EventEmitter<void>();
  @Output() printEvent = new EventEmitter<void>();

  onSubmit() {
    this.submitEvent.emit();
  }

  getDynamicPageTypeString(action: string) {
    // Result will be 'Edit/new/delete program' or 'Edit/new/delete client' (localized)
    if (this.pageType === 'client') {
      switch (action) {
        case 'new':
          return $localize`:@@PageToolbar.addNewTypeClient:New client`;
        case 'edit':
          return $localize`:@@PageToolbar.editPageTypeClient:Edit client / `;
        case 'delete':
          return $localize`:@@PageToolbar.deleteNewTypeClient:Delete client`;
        default:
          return '';
      }
    } else {
      switch (action) {
        case 'new':
          return $localize`:@@PageToolbar.addNewTypeProgram:New program`;
        case 'edit':
          return $localize`:@@PageToolbar.editPageTypeProgram:Edit program / `;
        case 'delete':
          return $localize`:@@PageToolbar.deleteNewTypeProgram:Delete program`;
        default:
          return '';
      }
    }
  }
}
